import React from 'react'
import "./Music.css";
import music from "./images/music.png"
import img from  "./images/music.png";
import { useEffect } from 'react';
import { useState } from 'react';
import YouTube from 'react-youtube'
function Music(props){
const[SongId, SetSongId] = useState('')

useEffect(()=>{
SetSongId(props?.Song)
},[props]);

const makeClick=(event)=>{
    document.getElementById('wwe').click();
    console.log("my")
   
}

    return (
       <div>
                {SongId =="" ? <span></span>: <iframe id="wwe" width="200" height="200" onLoad={makeClick} src={`https://www.youtube.com/embed/${SongId}?autoplay=1`}  frameborder="0" allow="autoplay">
</iframe> }
        </div>
    )
}

export default Music
